@font-face {
    font-family: "Ogg";
    src: url(./Ogg-Custom.woff2) format("woff2");
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Ogg";
    font-size: 1.5rem;
    color: #131313;
  }
  
  html,
  body {
    background-color: #dadada;
  }
  
  canvas {
    position: absolute;
    z-index: -2;
  }
  
  main {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
  }
  
  ul {
    position: absolute;
    left: 10%;
    top: 20%;
    height: 60%;
    width: 80%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  ul li {
    position: relative;
    border-bottom: 1px solid rgba(0, 0, 0, 0.118);
    cursor: pointer;
    transition: 1s;
  }
  
  ul li:hover {
    opacity: 1 !important;
  }